import { action, decorate, observable } from 'mobx';

class Store {
	public data: any[];
	constructor() {
		this.data = [['a', 'b', 'c'], [1, 2, 3]];
	}

	public fetchGoogleSpreadSheets(urlOrKey: string): void {
		console.log(urlOrKey);
	}
}

decorate(Store, {
	data: observable,
	fetchGoogleSpreadSheets: action,
});

const store = new Store();
export default store;



